		},

		props: {
			location: String,
			meal: String,
		},

		computed: {
			breakfastBaker: function() {
				console.log(this.times)
				return this.meal === "Breakfast" && this.location === "Baker";
			},

			breakfastMaseeh: function() {
				return this.meal === "Breakfast" && this.location === "Maseeh";
			},

			dinner59: function() {
				return this.meal === "Dinner" && this.location === "Maseeh";
			},

			dinner58: function() {
				return this.meal === "Dinner" && (this.location === "Simmons" || this.location === "McCormick");
			},

			dinner5half8half: function() {
				return this.meal === "Dinner" && (this.location === "Baker" || this.location === "Next");
			},
		},

		methods: {

			sendTime: function() {
				eventBus.$emit('time-for-location', this.times, this.location);
			},
		}

	}

