<template>
    <div id="login">
        <div class="loginForm thirdBackground roundedBorder">
            <div class="secondColorText"> 
                Login to SwipeBuddies!
            </div>
            
            <input type="text" class="roundedBorder" name="username" placeholder="Kerberos" v-model="loginUsername"/>
            <input type="password" class="roundedBorder" name="password" placeholder="Password" v-model="password"/>

            <div class="options">
                <button @click="login" class="loginButton mainBackground">
                    Login
                </button>
                <button @click="signup" class="createButton thirdBackground" style="border-style:solid">
                    Create Account
                </button>
            </div>

        </div>
    </div>
</template>

<script>

import Navigation from './Navigation.vue';
import { eventBus } from '../main'

export default {
  name: "login",

  data: function () {
      return {
        loginUsername: "",
        password: ""  
      };
  },

  created: function() {
    axios.get('/init')
        .then((response) => {
            console.log("init firebase");
        })
        .catch(err => {
            alert(err.response.data.error);
        })

    eventBus.$on('get-username', () => {
        eventBus.$emit('send-username', this.loginUsername);
    });
  },

  methods: {
      login: function() {
        const bodyContent = {username:this.loginUsername, password:this.password};
        axios.post('/users/signin', bodyContent)
            .then((response) => {
                this.$router.push('/home');
                eventBus.$emit('sign-in', this.loginUsername);
                eventBus.$emit('login', this.loginUsername);
            })
            .catch(err => {
                alert(err.response.data.error);
        });
      },

      signup: function() {
        this.$router.push('/signup');  
      }
  },

  components: {
    Navigation,
  },
}
</script>

