<template>
    <div id="signup">
        <div class="loginForm thirdBackground roundedBorder">
            <div class="secondColorText"> 
                Sign Up!
            </div>
            <div class='signup-textbox'>
                <input type="text" class="roundedBorder" name="username" placeholder="Kerberos" v-model="username"/>
                <input type="password" class="roundedBorder" name="password" placeholder="Password" v-model="password"/>
                <input type="password" class="roundedBorder" name="password" placeholder="Confirm Password" v-model="passwordConfirm"/>
            </div>

            <div class='role-radio'>
                <div class='label-radio'>
                    <input type="radio" id="giver" value="giver" v-model="role">
                    <label for="giver">Giver</label>
                </div>
                <div class='label-radio'>
                    <input type="radio" id="receiver" value="receiver" v-model="role">
                    <label for="receiver">Receiver</label>
                </div>
            </div>

            <div class="options">
                <button @click="signup" class="loginButton mainBackground">
                    Create Account
                </button>
            </div>

            <div class="navBack">
                <i class="material-icons" v-on:click="goLogin" style="cursor:pointer;"
                        title="Next Month" >navigate_before</i>
            </div>

        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
  name: "signup",
  data: function () {
      return {
        username: "",
        password: "" ,
        passwordConfirm: "",
        role: "giver",
      };
  },
  methods: {
      signup: function() {
        const bodyContent = {username:this.username, password:this.password, role:this.role};
        axios.post('/users/create', bodyContent)
            .then((response) => {
                
                this.goLogin();
            })
            .catch(err => {
                alert(err);
                // alert(err.response.data.error);
            });
      },
      goLogin : function() {
          this.$router.push('/login');
      }
  }
}
</script>

