<template>
	<div class='history-list'>

		<div v-if='upcoming.length'>
			<h3> Upcoming </h3>
			<MatchItem 
				v-for="match in upcoming"
				v-bind:key="match.date + match.buddy + match.meal"
				v-bind:match="match"
				v-bind:isPast=false
			/>
		</div>
		<div v-else>
			<h3> Upcoming </h3>
			<p>Currenty, you do not have upcoming matches.</p>
		</div>
		<br/>
		<div v-if='history.length'>
			<h3> History </h3>
			<MatchItem 
				v-for="match in history"
				v-bind:key="match.date + match.buddy + match.meal"
				v-bind:match="match"
				v-bind:isPast=true
			/>
		</div>
		<div v-else>
			<h3> History </h3>
			<p>You do not have any past matches.</p>
		</div>
		<modal v-if='reportFlag' name='Report' class='report-popup' height="auto" width="60%" :scrollable="false">
			<div class='report-group'>
				<p class='question-report'>Do you want to report {{matchForModal.buddy}} for not showing up?</p>
				<div class='report-button-group'>
					<button class='yes-button' @click='sendReport'>Yes</button>
					<button class='cancel-button-2' @click='closePopup'>Cancel</button>
				</div>
			</div>
		</modal>
		<modal v-if='cancelFlag' name='Cancel' class='report-popup' height="auto" width="60%" :scrollable="false">
			<div class='report-group'>
				<p class='question-report'>Do you want to cancel a {{matchFormodal.meal}} with {{matchForModal.buddy}} on {{matchForModal.date}}?</p>
				<div class='report-button-group'>
					<button class='yes-button' @click='cancel'>Yes</button>
					<button class='cancel-button-2' @click='closeCancelPopup'>No</button>
				</div>
			</div>
		</modal>
	</div>

</template>

<script>

	import MatchItem from "./MatchItem.vue";
	import {eventBus} from "../main";
	import firebase from 'firebase';

	export default {

		data: function() {
			return {
				history: [],
				upcoming: [],
				matchForModal: {location: "", time: "", meal: "", buddy: "", date: ""},
				reportFlag: false,
				cancelFlag: false,
			};
		},

		components: {
			MatchItem,
		},

		props: {
			username: String
		},

		created: function() {
            axios.get('/init')
                .then((response) => {
                    console.log("init firebase");
                })
                .catch(err => {
                    alert(err.response.data.error);
                })

      this.fetchData();

			eventBus.$on('reload-history', () => {
				this.fetchData();
			});

			eventBus.$on('show-report-modal', (res) => {
				this.matchForModal = res;
				this.reportFlag = true;
				this.$modal.show('Report');
			});

			eventBus.$on('show-cancel-modal', (res) => {
				this.matchForModal = res;
				this.cancelFlag = true;
				this.$modal.show('Cancel');
			});

		},

		// beforeMount: function() {
		//  	this.getUsername();
		//  	this.loadMatches();
		// },
		watch: {
    // call again the method if the route changes
    	'$route': 'fetchData'
  	},

		methods: {

			fetchData: function() {
				
				this.loadMatches();
			},

			closePopup: function() {
				this.$modal.hide('Report');
				this.reportFlag = false;
			},

			closeCancelPopup: function(){
				this.$modal.hide('Cancel');
				this.cancelFlag = false;
			},

			sendReport: function() {
				let role;
				if (this.match.role === 'giver') {
					role = 'receiver';
				} else {
					role = 'giver';
				}
				axios.post(`/users/reputation/report/${this.match.buddy}/${role}`)
				.then(response => {
					this.closePopup();
					this.disabled();
				})
				.catch(err => {
					alert(err.response.data.error);
				});
			},

			cancel: function() {

				axios.delete(`/matches/${this.match.date}/${this.match.meal}`)
				.then(response => {
					eventBus.$emit('reload-history');
				})
				.catch(err => {
					alert(err.response.data.error);
				});
			},

			loadMatches: function() {
				axios.get(`/matches/user/${this.username}`)
				// axios.get('/events/match/' + this.username) 
				.then(response => {
					let matches = response.data;
                    console.log(matches);
//				{ "2019-11-26": {
//             "breakfast": {
//                 "role": "giver",
//                 "buddy": "hello",
//                 "location": "Baker",
//                 "time": "9:30",
//             },
//             "dinner": {
//                 "role": "giver",
//                 "buddy": "hello",
//                 "location": "Simmons",
//                 "time": "18:30",
//             },
//				}
					let log = this.filter(matches);
					this.upcoming = this.sortAscending(log[0]);
					this.history = this.sortDescending(log[1]);
					
				})
				.catch(err => {
        	alert(err.response.data.error);
      	});
			},

			filter: function(matches) {
				let upcomingDate = [];
				let pastDate = [];
				let today = new Date();

				for (var day in matches) {
					let date = new Date(day);
					if (date >= today) {
						upcomingDate.push(day);
					} else {
						pastDate.push(day);
					}
				}

				let upcoming = [];
				for (let i = 0; i < upcomingDate.length; i++) {
					let meals = matches[upcomingDate[i]];
					for (var meal in meals) {
						let obj = {date: upcomingDate[i], meal: meal, role: meals[meal].role, buddy:meals[meal].buddy, location: meals[meal].location, time: meals[meal].time};
						upcoming.push(obj);
					}
				}

				let history = [];
				for (let i = 0; i < pastDate.length; i++) {
					let meals = matches[pastDate[i]];
					for (var meal in meals) {
						let obj = {date: pastDate[i], meal: meal, role: meals[meal].role, buddy: meals[meal].buddy, location: meals[meal].location, time: meals[meal].time};
						history.push(obj);
					}
				}

				return [upcoming, history]
			},

			sortAscending: function(array) {
				return array.sort(function(a, b) {
        	var x = new Date(a.date); 
        	var y = new Date(b.date);
        	if (x < y) {
        		return -1;
        	} else if (x > y) {
        		return 1;
        	} else {
        		if (a.meal === 'breakfast' || a.meal === 'brunch') {
        			return -1;
        		} else if (a.meal === 'dinner') {
        			return 1;
        		} else {
        			if (b.meal === 'breakfast') {
        				return 1;
        			} else if  (b.meal === 'dinner') {
        				return -1;
        			}
        		}
        	}
        	return 0;
      });
			},

			sortDescending: function(array) {
				return array.sort(function(a, b) {
        	var x = new Date(a.date); 
        	var y = new Date(b.date);
        	if (x < y) {
        		return 1;
        	} else if (x > y) {
        		return -1;
        	} else {
        		if (a.meal === 'breakfast' || a.meal === 'brunch') {
        			return 1;
        		} else if (a.meal === 'dinner') {
        			return -1;
        		} else {
        			if (b.meal === 'breakfast') {
        				return -1;
        			} else if  (b.meal === 'dinner') {
        				return 1;
        			}
        		}
        	}
        	return 0;
      });
			},

		},

	}

</script>


<style>

.history-list {
	overflow-y: scroll;
}

</style>