<template>
    <div id="timeslot" class="timeslot">
      <drag-select attribute="slotAttribute" @change="selected = $event" class='dragselect'>
      <div class="slot roundedBorder" v-bind:key="slot" v-for="slot in slots" 
      :slotAttribute="slot"  @mouseup='editTime'
      :class="{'item-selected-class': selected.includes(slot)}"
      v-bind:style="{ background: `${slotBackground(slot)}` }">
        <div class="time">
          {{slot[0].toLocaleTimeString()}}
        </div>
        <div class="locations">
          <div v-if='backgroundColor(slot[0])=="gray"' id="closedHall" class="roundedBorder">
            Closed
          </div>
          <div class="location second-color roundedBorder"
          v-bind:key="dorm" v-for="dorm in slot[1]">
            <i class="material-icons" style="font-size:14px;" 
            @click="slot[1].splice(slot[1].indexOf(dorm), 1);">
              cancel
            </i>
            <label> {{dorm}} </label>
          </div>
        </div>
      </div>
      </drag-select>
    </div>
        
</template>

<script>
import DragSelect from "drag-select-vue";
import { eventBus } from "../main";
 
export default {
  name: "timeslot",
  components: { DragSelect },
  data: function (){
      return {
          slots: [],
          interval: 30,
          startTime: 7,
          endTime: 20,
          dorms: ['Baker', 'Maseeh', 'McCormick', 'Next', 'Simmons'],
          selected: [],
          timesSelected: {},

          breakfastTimes: [7,10.5],
          brunchTimes: [10,12.5],
          lunchTimes: [11,14.5],
          dinnerTimes: [17,20.5],

          breakfast: {
            'Baker': [7,9.5], 
            'Simmons': [8,9.5], 
            'Maseeh':[8, 10.5],
            'McCormick': [8, 9.5],
            'Next': [8,9.5]
            },
          
          brunch: {
            'Baker': [10,12.5], 
            'Simmons': [10,12.5], 
            'Maseeh':[10,12.5],
            'McCormick': [10,12.5],
            'Next': [10,12.5]
          },
        
          lunch: {
            'Maseeh': [11,14.5],
          },

          dinner: {
            'Baker': [17.5, 20], 
            'Simmons': [17, 19.5], 
            'Maseeh':[17,20.5],
            'McCormick': [17, 19.5],
            'Next': [17.5, 20]
          },

      };
  },
  props: {
    day: Number, 
    month: Number, 
    year: Number,
    monthNames: Array,
    //Locations selected from the submission form
    breakfastLocations: Array,
    brunchLocations: Array,
    lunchLocations: Array, 
    dinnerLocations: Array,
    submittedSelections: Object,
  },
  methods: {
      created: function() {
        axios.get('/init')
          .then((response) => {
              console.log("init firebase");
          })
          .catch(err => {
              alert(err.response.data.error);
          })
      },
      isSelected: function(slot)
      {
        let timestamp = this.toDecimalHours(slot[0]);
        return this.timesSelected.hasOwnProperty(timestamp);
      },
      slotBackground: function(slot)
      {
        return this.isSelected(slot) ? this.backgroundColor(slot[0]) : 'gray';
      },
      backgroundColor: function(time)
      {
        let food = this.typeServed(time);
        if(food == 'breakfast')
        {
          return '#3C82F9';
        }
        else if(food == 'lunch')
        {
          return '#783cf9';
        }
        else if(food == 'brunch')
        {
          return '#783cf9';
        }
        else if(food == 'dinner')
        {
          return '#3cf9b4';
        }
        else
        {
          return 'gray';
        }
      },
      /**
       * Get the type of food being served at a certain time
       * @param time The time given (js: new Date())
       * @return A string indicating what is being served at that time
       */
      typeServed(time)
      {
        let hour = this.toDecimalHours(time);
        if(hour >= this.breakfastTimes[0] && hour < this.breakfastTimes[1] && !this.isWeekend)
        {
          return 'breakfast';
        }
        else if(hour >= this.lunchTimes[0] && hour < this.lunchTimes[1] && !this.isWeekend)
        {
          return 'lunch';
        }
        else if(hour >= this.brunchTimes[0] && hour < this.brunchTimes[1] && this.isWeekend)
        {
          return 'brunch';
        }
        else if((hour >= this.dinnerTimes[0] && hour < this.dinnerTimes[1]))
        {
          return 'dinner';
        }
        else
        {
          return undefined;
        }
      },
      editTime: function()
      {
        //if selected time is in timeSelected, means to remove, otherwise insert
        for(let i = 0; i < this.selected.length; i++) {
          let slot = this.selected[i];
          let timestamp = slot.split(',')[0];
          let time = new Date(Date.parse(timestamp));
          let hour = this.toDecimalHours(time);
          let slotIndex = (hour - this.startTime)/.5;

          if(this.timesSelected.hasOwnProperty(hour))
          {
            //Remove
            delete this.timesSelected[hour];
          }
          else if(this.typeServed(time) != undefined && this.slots[slotIndex][1].length > 0)
          {
            //Insert
            this.timesSelected[hour] = slot;
          }
        }

        //Hackish solution...Do not remove.
        this.slots.push("drag select is hard.");
        this.slots.pop();

        this.emitSelections();
      },

      /**
       * Determine if a hall is open
       * @param hall The dining hall
       * @param time The time 
       * @return boolean
       */
      diningHallIsOpen(hall, time)
      {
        let hour = this.toDecimalHours(time);
        if(this.breakfast.hasOwnProperty(hall))
        {
          if(hour >= this.breakfast[hall][0] && hour < this.breakfast[hall][1] && !this.isWeekend)
          {
            return true;
          }
        }
        if(this.lunch.hasOwnProperty(hall))
        {
          if(hour >= this.lunch[hall][0] && hour < this.lunch[hall][1] && !this.isWeekend)
          {
            return true;
          }
        }
        if(this.brunch.hasOwnProperty(hall))
        {
          if(hour >= this.brunch[hall][0] && hour < this.brunch[hall][1] && this.isWeekend)
          {
            return true;
          }
        }
        if(this.dinner.hasOwnProperty(hall))
        {
          if(hour >= this.dinner[hall][0] && hour < this.dinner[hall][1])
          {
            return true;
          }
        }

        return false;
      },
      getDifference: function(newList, oldList)
      {
        var added = [];
        var deleted = [];

        var newSet = new Set(newList);
        var oldSet = new Set(oldList);

        var difference = (newList.filter( x => !oldSet.has(x))).concat(oldList.filter( x => !newSet.has(x)));
        difference.forEach(function(x)
        {
          if(newSet.has(x))
            added.push(x);
          else
            deleted.push(x); 
        });
        return {'added': added, 'deleted': deleted};
      },
      /**
       * Add a dining hall to an interval of timeslots
       * @param hall The hall to be added
       * @param startTime The start of the interval
       * @param endTime The end of the interval
       */
      addDiningHall: function(hall, startTime, endTime)
      {
        for(let i = 0; i < this.slots.length; i++)
        {
          var slot = this.slots[i];
          var hour =  this.toDecimalHours(slot[0]);
          if(hour >= startTime && hour <= endTime)
          {
            
            if(!slot[1].includes(hall) && this.diningHallIsOpen(hall, slot[0]))
              slot[1].push(hall);
          }
        }
        this.emitSelections();
      },
      /**
       * Delete a dining hall to an interval of timeslots
       * @param hall The hall to be added
       * @param startTime The start of the interval
       * @param endTime The end of the interval
       */
      deleteDiningHall: function(hall, startTime, endTime)
      {
        for(let i = 0; i < this.slots.length; i++)
        {
          var slot = this.slots[i];
          var hour =  this.toDecimalHours(slot[0]);

          if(hour >= startTime && hour <= endTime)
          {
            var index = slot[1].findIndex(x => x == hall);
            if(index != undefined)
              slot[1].splice(index, index +1);
          }
        }
        this.emitSelections();
      },
      toDecimalHours: function(time)
      {
        return time.getHours() + time.getMinutes()/60;
      },
      /**
       * Gets all the infomation on what the user selected
       * @returns A list of time slots with [time, locations]
       */
      emitSelections: function()
      {
        let selectedTimeSlots = {
          'breakfast':{
            'Baker': [], 
            'Simmons': [], 
            'Maseeh':[],
            'McCormick': [],
            'Next': []}, 
          'brunch':{
            'Baker': [], 
            'Simmons': [], 
            'Maseeh':[],
            'McCormick': [],
            'Next': []},
          'lunch': { 
            'Maseeh':[]}, 
          'dinner':{
            'Baker': [], 
            'Simmons': [], 
            'Maseeh':[],
            'McCormick': [],
            'Next': []} 
        };
        for(let i = 0; i < this.slots.length; i++)
        {
          if(this.isSelected(this.slots[i]))
          {
            let time = this.slots[i][0];
            let meal = this.typeServed(this.slots[i][0]);
            let locations = this.slots[i][1];
            for(let i = 0; i < locations.length; i++)
            {
              let location = locations[i];
              selectedTimeSlots[meal][location].push(time.getHours().toString() + ":" + time.getMinutes().toLocaleString('en-US', {minimumIntegerDigits: 2, useGrouping:false}));
            }            
          }
        }
        eventBus.$emit('selection-change', selectedTimeSlots);
      },
      intializeTimeSlots(){
        var time = new Date();
        time.setHours(this.startTime,0,0,0);

        //console.log(this.submittedSelections);
        while(time.getHours() <= this.endTime)
        {
          //Start with time slots as empty ...
          var locations = []
          
          var meal = this.typeServed(time);
          
          if(this.submittedSelections[meal] != undefined)
          {
            //Go through each location ...
            const dorms = Object.keys(this.submittedSelections[meal]);
            for(const dorm of dorms)
            {
              //check if inside ...
              let t = time.getHours().toString() + ":" + time.getMinutes().toLocaleString('en-US', {minimumIntegerDigits: 2, useGrouping:false});
              
              
              
              if(this.submittedSelections[meal][dorm].includes(t))
                locations.push(dorm);
            }
          }

          var slot = [time, locations];
          this.slots.push(slot);
          if(locations.length > 0)
            this.timesSelected[this.toDecimalHours(time)] = slot;
          

          time = new Date(time.getTime() + this.interval*60000);
        }
      },
  },
  computed: {
    isWeekend: function() {
      let ymd = new Date(this.year, this.month, this.day);
      return ymd.getDay() === 0 || ymd.getDay() === 6;
    },
  },

  watch: {
    breakfastLocations: function (newLocations, oldLocations) {
      //Get difference in locations
      var difference = this.getDifference(newLocations, oldLocations);
      
      difference['added'].forEach(x => this.addDiningHall(x, this.breakfastTimes[0], this.breakfastTimes[1]));
      difference['deleted'].forEach(x => this.deleteDiningHall(x, this.breakfastTimes[0], this.breakfastTimes[1]));
    },
    brunchLocations: function (newLocations, oldLocations) {
      //Get difference in locations
      var difference = this.getDifference(newLocations, oldLocations);
      
      difference['added'].forEach(x => this.addDiningHall(x, this.brunchTimes[0], this.brunchTimes[1]));
      difference['deleted'].forEach(x => this.deleteDiningHall(x, this.brunchTimes[0], this.brunchTimes[1]));
    },
    lunchLocations: function (newLocations, oldLocations) {
      //Get difference in locations
      var difference = this.getDifference(newLocations, oldLocations);
      
      difference['added'].forEach(x => this.addDiningHall(x, this.lunchTimes[0], this.lunchTimes[1]));
      difference['deleted'].forEach(x => this.deleteDiningHall(x, this.lunchTimes[0], this.lunchTimes[1]));
    },
    dinnerLocations: function (newLocations, oldLocations) {
      //Get difference in locations
      var difference = this.getDifference(newLocations, oldLocations);
      
      difference['added'].forEach(x => this.addDiningHall(x, this.dinnerTimes[0], this.dinnerTimes[1]));
      difference['deleted'].forEach(x => this.deleteDiningHall(x, this.dinnerTimes[0], this.dinnerTimes[1]));
    },
    submittedSelections: function()
    {
      this.intializeTimeSlots();
    },
  },
  
}
</script>

<style scoped>
    .timeslot {
      display: flex;
      /* max-height: 600px; */
      /* width: 100px; */
      /* overflow-y: auto; */
    }

    div .slot {
        display: flex;
        flex-direction: column;
        min-width: 100px;
        padding: 5px;
        border-radius: 5px;

        flex-wrap: wrap;

        border-style:solid;
        border-width: 1px;
    }

    div .locations{
        display: flex;
        flex-direction: row;
        padding: 1px;
        margin: 3px;
    }

    div .location {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        padding: 2px;
        margin: 1px;

    }

    label {
        padding: 5px;
    }


    .first-color {
        background: #98d2ff;
    }

    .second-color {
        background: #98d2ff;
    }

    .third-color {
        background: #98d2ff;
    }

    #closedHall {
      font-size: 16px;
      font-weight: bold;
      background: red;
      padding: 2px; 
    }
</style>
