<template>
  <div id="app" class='content'>
    <Navigation 
      v-bind:username="username"/>
    <div class='maincontent'>
      <router-view
      :username="username"
      :role="role"/>
    </div>
  </div>
</template>

<script>
import Navigation from "./components/Navigation.vue";
import { eventBus } from "./main";

export default {
  name: "app",
  data: function()
  {
    return {
      username: undefined,
      role: undefined,
    }
  },
  components: {
    Navigation,
  },
  methods: {
    getUsername : function() {
      axios.get('/users/session', {})
      .then((response) => {
        if(response.status == 200)
        {
          //User is logged in
          this.username = response.data;
          // this.$router.push("/home");
          eventBus.$emit('sign-in');
          this.getUserRole();
        }
        else 
        {
          // this.$router.push("/login");
          eventBus.$emit('log-out');
        }
      })
      .catch(err => {
        alert(err.response.data.error);
        this.errors.push(err.response.data.error)
      })
    },
    getUserRole : function() {
      axios.get('/users/role', {})
      .then((response) => {
        //User is logged in
        this.role = response.data;
      })
      .catch(err => {
        alert(err.response.data.error);
        this.errors.push(err.response.data.error)
      })
    },
  },
  created: function() {
    eventBus.$on('login', (username) => {
      this.getUsername();
    });
    eventBus.$on('setRole', (role) =>
    {
      this.role = role;
    });
  },
  beforeMount: function() {
    console.log("Refresh");
    //Check if signed in or not
    if(this.username == undefined) {
      this.getUsername();
    }

  }
};

</script>

<style>

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px;
  }

  .maincontent
  {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 10px;
  }

</style>