<template>
	<div class='match-item'>
		<div class='match-info'>
			<p class='match-sub-info'>Date: {{match.date}}</p>
			<p class='match-sub-info'>Meal: {{match.meal}}
			<p class='match-sub-info'>Buddy: {{match.buddy}}</p>
			<p class='match-sub-info'>Location: {{match.location}}</p>
			<p class='match-sub-info'>Time: {{match.time}}</p>
		</div>
		<div v-if="isPast">
			<button class='report-button' @click='report'>Report</button>
		</div>
		<div v-else>
			<button class='cancel-button'@click='showCancel'>Cancel</button>
		</div>
	</div>
</template>

<script>

	import { eventBus } from '../main';

	export default {
		  created: function() {
		      axios.get('/init')
		          .then((response) => {
		              console.log("init firebase");
		          })
		          .catch(err => {
		              alert(err.response.data.error);
		          })
		  },

		data: function() {
			return {

			};
		},

		props: {
			match: Object,
			isPast: Boolean,
		},

		methods: {

			report: function() {
				eventBus.$emit('show-report-modal', this.match);
				//this.$modal.show('Report');
			},

			showCancel: function() {
				eventBus.$emit('show-cancel-modal', this.match);
				//this.$modal.show('Cancel');
			},

			cancel: function() {

				axios.delete(`/matches/${this.match.date}/${this.match.meal}`)
				.then(response => {
					eventBus.$emit('reload-history');
				})
				.catch(err => {
					alert(err.response.data.error);
				});
			},

			closePopup: function() {
				this.$modal.hide('Report');
			},

			closeCancelPopup: function(){
				this.$modal.hide('Cancel');
			},

			sendReport: function() {
				let role;
				if (this.match.role === 'giver') {
					role = 'receiver';
				} else {
					role = 'giver';
				}
				axios.post(`/users/reputation/report/${this.match.buddy}/${role}`)
				.then(response => {
					this.closePopup();
					this.disabled();
				})
				.catch(err => {
					alert(err.response.data.error);
				});
			},

			disabled: function() {
				let button = document.querySelector('.report-button')
				button.disabled = true;
				button.innerHTML = 'Reported';
				button.style.backgroundColor = 'gray';
			}
		},
	}
	
</script>

