<template>
    <header id="navigation" class="mainBackground roundedBorder">
        <div class="MainTitle">
            Swipe Buddies
        </div>
        <div v-if='isSignedIn' class='button-group'>
          <div> 
            Welcome: {{username}}
          </div>
          <button class="secondBackground" @click='goMatch' v-if='isAdmin'> MatchTest </button>
        	<button class="secondBackground" @click='goHome'> Home </button>
        	<button class="secondBackground" @click='goHistory'> History </button>

          <button class="secondBackground" @click='goAccount'> Account </button>
          <button class="secondBackground" @click='logOut'> Logout </button>
        </div>
        <div class="loginstuff" v-else>
          <a href='/#/loginoidc' class='login-link roundedBorder fourthHarmony'> Login </a>
        </div>
    </header>
</template>

<script>

import { eventBus } from "../main"

export default {
  name: "navigation",

  data: function() {
    return {
  		isSignedIn: false,
      isAdmin: false,
      currentDay: 0,
      currentMonth: 0,
      currentYear: 0,
    }
  },
  props: {
    username: String,
  },

  beforeMount: function() {
    var tomorrow = new Date(new Date().getTime() + 24 * 60 * 60 * 1000);
    
    this.currentDay = tomorrow.getDate();
    this.currentMonth = tomorrow.getMonth()+1;
    this.currentYear = tomorrow.getFullYear();
  },

  computed: {
    dateString: function() {
      return this.currentYear + "-" + 
             this.currentMonth + "-" + 
             this.currentDay;
    },
  },

  created: function() {
    axios.get('/init')
        .then((response) => {
            console.log("init firebase");
        })
        .catch(err => {
            alert(err.response.data.error);
        })
  	eventBus.$on('sign-in', () => {
  		this.isSignedIn = true;
      this.checkAdmin();
  	});
  },

  methods: {
  	goHistory: function() {
      this.$router.push('/history');
  		eventBus.$emit('view-history');
  	},

  	goHome: function() {
  		this.$router.push('/home');
  	},

    goMatch: function() {
      axios.get('/matches/day/' + this.dateString)
      .then((response) => {
        console.log("YAY!");
        
      })
      .catch(err => {
        alert(err.response.data.error);
      })
    },

    goAccount: function() {
      this.$router.push('/account');
    },
    checkAdmin: function() {
      axios.get('/users/admin')
      .then((response) => {
        this.isAdmin = response.data["result"];
      })
      .catch(err => {
        alert(err.response.data.error);
      })
    },

  	logOut: function() {
  		axios.post('/users/signout')
      .then((response) => {
      	this.$router.push('/login');
        this.isSignedIn = false;
      })
      .catch(err => {
        alert(err.response.data.error);
      })
  	},
  },

}
</script>

<style>

  .MainTitle {
    color:white;
    font-weight: bold;
    font-size: 30px;
  }
  
  .button-group {
    display: flex;
    direction: row;
    justify-content: flex-end;
    align-items: center;
  }

  .button-group button {
    height: 40px;
    min-width: 80px;
    margin: 0px 10px 0px;
  }

  .button-group div {
    font-size: 18px;
    margin-right: 20px;
    font-weight: bold;
    color: white;
  }

  .login-link {
    margin-right: 20px;
    text-decoration: None;
    display: flex;
    align-items: center;
    padding: 12px;
  }
  
  .loginstuff {
    display: flex;
    flex-direction: row;
  }
</style>