                </button>
                <button @click="signup" class="createButton thirdBackground" style="border-style:solid">
                    Create Account
                </button>
            </div>

        </div>
    </div>
</template>

<script>
import Navigation from './Navigation.vue';
import { eventBus } from '../main'
export default {
  name: "login",
  data: function () {
      return {
        username: "",
        password: ""  
      };
  },
  created: function() {
    axios.get('/init')
        .then((response) => {
            console.log("init firebase");
        })
        .catch(err => {
            alert(err.response.data.error);
        })
    eventBus.$on('get-username', () => {
        eventBus.$emit('send-username', this.username);
    });
  },
  methods: {
      login: function() {
        this.$router.push('/authorized-login');
      },
      signup: function() {
        this.$router.push('/authorized-create');
      }
  },
  components: {
    Navigation,
  },
}
</script>

