<template>
  <div id="calendar">
    <div>
      <div class="fifthBackground box">
        <div class="calendarMenu calendarMenuColor">
          <i class="material-icons" v-on:click="previousMonth" style="cursor:pointer;font-size:40px;"
                        title="Previous Month" >navigate_before</i>

          <label> {{monthNames[month]}} {{year}} </label>
          <i class="material-icons" v-on:click="nextMonth" style="cursor:pointer;font-size:40px;"
                        title="Next Month" >navigate_next</i>
        </div>
        <div class='calendar'>

          <div class="daysofWeek">
            <div class="date"> Sun </div>
            <div class="date"> Mon </div>
            <div class="date"> Tues </div>
            <div class="date"> Wed </div>
            <div class="date"> Thur </div>
            <div class="date"> Fri </div>
            <div class="date"> Sat </div>
          </div>

          <!-- Display the mapping of days (ie: monday) to dates (numbers) 
            as specified by the data var "weeks"-->
          <div class="week" v-bind:key="week" v-for="week in weeks">
            <label class="date" v-bind:key="day" v-for="day in week">
              <!-- Style differently to display the current date -->
              <div v-if="day < currentDate && month <= currentMonth && year <= currentYear || (month < currentMonth && year <= currentYear)" style="color:gray">
                {{day}}
              </div>
              <div @click="getForm(day, month, year)" v-else-if="selectedDay==day && month==selectedMonth && year==selectedYear" class="selectable roundedBorder selected">
                {{day}}
              </div>
              <div @click="getForm(day, month, year)" v-else-if="currentDate==day && month==currentMonth && year==currentYear"
                  class="currentDate selectable roundedBorder">
                {{day}}
              </div>
              <div @click="getForm(day, month, year)" v-else class="selectable roundedBorder">
                {{day}}
              </div>
            </label>
          </div>

        </div>
      </div>
    </div>

    <modal name="timeSelection" class="modal"
      height="auto" width="60%" :scrollable="true">

      
      <div class="popup">
        <div class="exit">
        <i class="material-icons" style="cursor:pointer;font-size: 30px;"
                          @click='hide'>cancel</i>
        </div>
        
        <Submission 
          v-bind:day="selectedDay" 
          v-bind:month="selectedMonth"
          v-bind:year="selectedYear"
          v-bind:monthNames="monthNames"
          v-bind:role="role"
        />

      </div>
    </modal>
  </div>
</template>

<script>
import Submission from "./Submission.vue";
import TimeSlot from "./TimeSlot.vue";

export default {
  name: "calendar",
  components: {
    Submission, TimeSlot,
  },
  created: function() {
      axios.get('/init')
          .then((response) => {
              console.log("init firebase");
          })
          .catch(err => {
              alert(err.response.data.error);
          })
  },

  data: function() {
    return {
      weeks: {},        //Mapping from dates to days
      month: 0,         //The displayed month
      year: 0,          //The displayed year
      currentDate: 0,   //Current Date
      currentMonth: 0,  //Current Month
      currentYear: 0,   //Current Year
      selectedDay: 0,   //Selected Day
      selectedMonth: 0, //Selected Month  
      selectedYear: 0,  //Selected Year
      monthNames: ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"]
    }
  },
  props : {
    selectedDate : String,
    role: String,
  },

  methods: 
  {
    show () {
      this.$modal.show('timeSelection', {height: 'auto', width:'50%', scrollable:'true', adaptive:'true'});
    },
    hide () {
      this.$modal.hide('timeSelection');
    },
    /**
      Gives a list of weeks representing a calendar
      Each value in the list represents a week of 7 days,
      from sunday to saturday
      ie:
        week1 = [0, 0, 1, 2, 3, 4, 5]
        represents a week where the first day of the month starts on tuesday.
      @param {int} month The month
      @param {int} year The year
      @returns {list} Represents the calendar for that month and year.
    */
    getCalendar : function(month, year) 
    {
      //Figure out how many days in the current month 
      var monthDays = new Date(year, month + 1, 0).getDate();
      //Figure out what date the month starts in 
      var firstDay = new Date(year, month, 1).getDay();
      //Then full out the data structure

      var date = 1;
      var day = firstDay;
      var result = []

      while(date <= monthDays)
      {
        var week = new Array(7).fill(0);
        week = week.map(function (value, index) {
          if(index < day || date > monthDays) return "";
          var currentDate = date;
          date++;
          day++;
          return currentDate;
        });
        day = 0;
        result.push(week);
      }

      return result;
    },
    /**
     * Makes the view switch to the next month
     */
    nextMonth : function()
    {
      this.year = this.month + 1 > 11 ? this.year + 1 : this.year;
      this.month = this.month + 1 > 11 ? 0 : this.month + 1;
      this.weeks = this.getCalendar(this.month, this.year);
    },
    /**
     * Makes the view switch to the previous month
     */
    previousMonth: function()
    {
      this.year = this.month - 1 < 0 ? this.year - 1 : this.year;
      this.month = this.month - 1 < 0 ? 11 : this.month - 1;
      this.weeks = this.getCalendar(this.month, this.year);
    },
    getForm: function(day, month, year)
    {
      this.selectedDay = day;
      this.selectedMonth = month;
      this.selectedYear = year;
      this.show();
    }
  },
  beforeMount() {
    var today = new Date();
    
    this.currentDate = today.getDate();
    this.currentMonth = today.getMonth();
    this.currentYear = today.getFullYear();
    
    this.weeks = this.getCalendar(today.getMonth(), today.getFullYear());
    this.month = today.getMonth();
    this.year = today.getFullYear();

    this.getForm(this.currentDate, this.currentMonth, this.currentYear);
  }
};
</script>

