<template>
    <div id="signup">
        <div class="loginForm thirdBackground roundedBorder">
            <h2 class="secondColorText"> 
                Sign Up for SwipeBuddies!
            </h2>

            <div class='settingDescription'>
                Here you can change whether you want to donate or recieve food swipes!
            </div>

            <div class='role-radio selection'>
                <div class='label-radio'>
                    <input type="radio" id="giver" value="giver" v-model="role">
                    <label for="giver">Giver</label>
                </div>
                <div class='label-radio'>
                    <input type="radio" id="receiver" value="receiver" v-model="role">
                    <label for="receiver">Receiver</label>
                </div>
            </div>

            <div class="options">
                <button @click="signup" class="loginButton mainBackground">
                    Create Account
                </button>
            </div>

        </div>
    </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";
export default {
  name: "signup",
  data: function () {
      return {
        username: "",
        password: "" ,
        passwordConfirm: "",
        role: "giver",
      };
  },
  methods: {
      signup: function() {
        const bodyContent = {username:this.username, password:this.password, role:this.role};
        eventBus.$emit('setRole', this.role);
        axios.post('/users', bodyContent)
            .then((response) => {
                this.$router.push('/home');
            })
            .catch(err => {
                alert(err);
                // alert(err.response.data.error);
            });
      },
  }
}
</script>

