<template>
	<div id="submission">
		<TimeSlot
		  v-bind:brunchLocations="brunchLocations"
		  v-bind:breakfastLocations="breakfastLocations"
		  v-bind:lunchLocations="lunchLocations"
		  v-bind:dinnerLocations="dinnerLocations"
		  v-bind:day="day" 
          v-bind:month="month"
          v-bind:year="year"
		  v-bind:submittedSelections="submittedSelections"/>
		<div>
			<div class="submission-form submissionColor roundedBorder">
				<h3 v-if="role=='giver'"> Donate Meal Form: {{day}} {{monthNames[month]}} {{year}} </h3>
				<h3 v-else> Request Meal Form: {{day}} {{monthNames[month]}} {{year}} </h3>
				
				<h3> Choose Dining Halls </h3>
				<div v-if="isWeekend">
					<h4> Brunch </h4>
					<div class='location-list'>
						<div class='item'>
							<input type='checkbox' id="baker" value="Baker" v-model="brunchLocations">
							<label for="baker">Baker</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="maseeh" value="Maseeh" v-model="brunchLocations">
							<label for="maseeh">Maseeh</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="mcCormick" value="McCormick" v-model="brunchLocations">
							<label for="mcCormick">McCormick</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="next" value="Next" v-model="brunchLocations">
							<label for="next">Next</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="simmons" value="Simmons" v-model="brunchLocations">
							<label for="simmons">Simmons</label>
						</div>
					</div>
				</div>

				<div v-else>
					<h4>Breakfast</h4>
					<div class='location-list'>
						<div class='item'>
							<input type='checkbox' id="baker" value="Baker" v-model="breakfastLocations">
							<label for="baker">Baker</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="maseeh" value="Maseeh" v-model="breakfastLocations">
							<label for="maseeh">Maseeh</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="mcCormick" value="McCormick" v-model="breakfastLocations">
							<label for="mcCormick">McCormick</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="next" value="Next" v-model="breakfastLocations">
							<label for="next">Next</label>
						</div>
						<div class='item'>
							<input type='checkbox' id="simmons" value="Simmons" v-model="breakfastLocations">
							<label for="simmons">Simmons</label>
						</div>
					</div>

					<h4>Lunch</h4>
					<div class='location-list'>
						<div class='item'>
							<input type='checkbox' id="maseeh" value="Maseeh" v-model="lunchLocations">
							<label for="maseeh">Maseeh</label>
						</div>
					</div>
				</div>

				<h4>Dinner</h4>
				<div class='location-list'>
					<div class='item'>
						<input type='checkbox' id="baker" value="Baker" v-model="dinnerLocations">
						<label for="baker">Baker</label>
					</div>
					<div class='item'>
						<input type='checkbox' id="maseeh" value="Maseeh" v-model="dinnerLocations">
						<label for="maseeh">Maseeh</label>
					</div>
					<div class='item'>
						<input type='checkbox' id="mcCormick" value="McCormick" v-model="dinnerLocations">
						<label for="mcCormick">McCormick</label>
					</div>
					<div class='item'>
						<input type='checkbox' id="next" value="Next" v-model="dinnerLocations">
						<label for="next">Next</label>
					</div>
					<div class='item'>
						<input type='checkbox' id="simmons" value="Simmons" v-model="dinnerLocations">
						<label for="simmons">Simmons</label>
					</div>
				</div>
					
				<div>
					<h4>Repeat until End of Semester</h4>
					<input type='checkbox' id="repeat" value="repeat" v-model="repeat">
					<label for="repeat">weekly</label>
				</div>
				<br/>
				<br/>
				<button v-if="!confirmation" class='buttonColor' @click='confirmation=true'>Submit</button>
				<div v-else class='confirmationButtons'>
					<button class='buttonColor' @click=submitForm>Confirm</button>
					<button class='buttonColor' @click='confirmation=false' style="background-color:red;">Cancel</button>
				</div>
				<div v-if='success' class='success-message'>
					<br/>
					{{success}}
				</div>

				<div v-if='errors.length' class='error-message'>
					<br/>
					<ul>
						<li v-for='error in errors' v-bind:key='error.id'>
							{{error}}
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from "axios";
	import { eventBus } from "../main";

	import firebase from 'firebase';

	import OperatingHours from "./OperatingHours.vue";
	import TimeSlot from "./TimeSlot.vue";

	export default {
		name: "submission",
		data: function() {
			return {
				meal: "",
				brunchLocations: [],
				lunchLocations: [],
				breakfastLocations: [],
				dinnerLocations: [],
				repeat: false,
				success: "",
				errors: [],
				selections: {},
				submittedSelections: {},
				confirmation: false,
			};
		},
		
		props: {
			day: Number, 
			month: Number, 
			year: Number,
			monthNames: Array,
			role: String,
		},

		components: {
			OperatingHours, TimeSlot,
		},

		created: function() {
			axios.get('/init')
                .then((response) => {
                    console.log("init firebase");
                })
                .catch(err => {
                    alert(err.response.data.error);
                })

			eventBus.$on('time-for-location', (t, l) => {
				this.time[l] = t;
			});

			eventBus.$on('selection-change', (selections) => {
				this.selections = selections;
			});

			const selectedDate = `${this.year}-${this.month + 1}-${this.day}`;
			const url = '/events/' + selectedDate;
			axios.get(url, {})
			.then((response) => {
				this.submittedSelections = response.data;
				this.selections = this.submittedSelections;
			})
			.catch(err => {

				alert(err.response.data.error);
				this.errors.push(err.response.data.error)
			});


		},

		computed: {

			isWeekend: function() {
				let ymd = new Date(this.year, this.month, this.day);
				return ymd.getDay() === 0 || ymd.getDay() === 6;
			},
		},

		methods: {
			checkSemester(date, semester)
			{	
				const fallEnd = {'month': 11, 'day': 25};
				const springEnd = {'month': 5, 'day': 5};
				var result =  semester ==
					 (date.getDay() <= springEnd['day'] && date.getMonth() <= springEnd['month'] ? 'spring': 'fall');
				return result;
			},
			submitForm: function(evt) {
				// create event 
				console.log("evt: ", evt);
				evt.preventDefault();

				const date = new Date(this.year, this.month, this.day);
				const semester = this.checkSemester(date, 'fall') ? 'fall' : 'spring';
				const dayOfWeek = date.getDate();

				const meals = Object.keys(this.selections);
				const selectedDates = [`${this.year}-${this.month+1}-${this.day}`];
				if(meals.length == 0)
				{
					//Then display text saying you must fill out forms 
					this.errors.push("Fill form out before submitting");
				}

				date.setDate(date.getDate() + 7);
				while(this.checkSemester(date, semester) && this.repeat)
				{
					selectedDates.push(date.getFullYear().toString() + '-' + (date.getMonth() + 1).toString() + '-' + date.getDate().toString());
					date.setDate(date.getDate() + 7);
				}

				for(const selectedDate of selectedDates){
					for (const meal of meals) {
						this.submitMeal(meal, this.selections[meal], selectedDate);
					}
				}		
			},

			submitMeal: function(meal, dormsAndTimes, selectedDate) {
				const bodyContent = {
					day : selectedDate, 
					meal : meal, 
					event_data : dormsAndTimes,
				}
			   	axios.post('/events', bodyContent)
                	.then((response) => {
						this.success = "You successfully submitted a request."
						console.log("create-event-sucess");
						this.submittedSelections = response.data[selectedDate];
						// console.log("response: ", response);
                    })
                    .catch(err => {

						alert(err.response.data.error);
						this.errors.push(err.response.data.error)
                    }).then(() => {
						// console.log(this.time);
						// console.log(this.locations);
                        //this.resetForm();
                        this.clearMessages();
                });	 
			},
			resetForm: function() {
				this.meal = "";
				this.changeMeal();
			},

			changeMeal: function() {
				this.locations = [];
				this.time = {'Baker':[], 'Maseeh':[], 'McCormick':[], 'Next':[], 'Simmons':[]};
				this.repeat = false;
			},

			clearMessages: function() {
				setInterval(() => {
					this.errors.pop();
					this.success = "";
				}, 5000);
			},

		},

		watch: {

			day: function(newVal, oldVal) {
				this.resetForm();
			},

			month: function(newVal, oldVal) {
				this.resetForm();
			},

			year: function(newVal, oldVal) {
				this.resetForm();
			}
		}

	}

</script>

