import Login from './components/Login.vue';
import Calendar from './components/Calendar.vue';
import SignUp from './components/SignUp.vue';
import History from './components/History.vue';
import Account from './components/Account.vue';
import SignUpOIDC from './components/SignUpOIDC.vue';
import LoginOIDC from './components/LoginOIDC.vue';


const routes = [
    { path : '/', component: Login },
    { path: '/login', component: Login },
    { path: '/home', component: Calendar },
    { path: '/signup', component: SignUp },
    { path: '/history', component: History },
    { path: '/account', component: Account},
    { path: '/signupoidc', component: SignUpOIDC},
    { path: '/loginoidc', component: LoginOIDC},
    
    
    { path : '/authorized-create', 
        beforeEnter(to, from, next) {
            window.location = 'https://oidc.mit.edu/authorize?' + 'client_id=8b68a831-dc0a-4809-b334-715594ab3c71'
            + '&response_type=code'
            + '&scope=openid profile email'
            + '&redirect_uri=http://localhost:8080/users/authorized_create'
            // + '&redirect_uri=http://swipe-buddies.herokuapp.com/users/authorized_create'
            + '&state=secure'
            + '&nonce=75429'
        }
    },
    { path : '/authorized-login', 
        beforeEnter(to, from, next) {
            window.location = 'https://oidc.mit.edu/authorize?' + 'client_id=8b68a831-dc0a-4809-b334-715594ab3c71'
            + '&response_type=code'
            + '&scope=openid profile email'
            + '&redirect_uri=http://localhost:8080/users/authorized_signin'
            // + '&redirect_uri=http://swipe-buddies.herokuapp.com/users/authorized_signin'
            + '&state=secure'
            + '&nonce=75429'
        }
    },
];

export default routes;
