<template>
    <div id="account" class="thirdBackground  roundedBorder">
        <h1> User Settings </h1>
        <div class="setting">
            <h2>
                Change User Status
            </h2>
            <div class='roleDescription'>
                Currently, you are a <strong>{{role}}</strong>.
            </div>
            <br/>
            <div class='settingDescription'>
                Here you can change whether you want to donate or recieve food swipes!
            </div>
            <div class='role-radio'>
                <div class='label-radio'>
                    <input type="radio" id="giver" value="giver" v-model="newRole">
                    <label for="giver">Giver</label>
                </div>
                <div class='label-radio'>
                    <input type="radio" id="receiver" value="receiver" v-model="newRole">
                    <label for="receiver">Receiver</label>
                </div>
            </div>
        </div>
        <div class="setting save">
            <button @click="submitSettings" class="mainBackground blueButton"> Save Changes </button>
        </div>
        <div v-if='success' class='success-message'>
            <br/>
            {{success}}
		</div>
        <div v-if='error' class='error-message'>
            <br/>
            {{error}}
        </div>
        <modal name='Confirm' class='report-popup' height="auto" width="60%" :scrollable="false">
            <div class='report-group'>
                <p class='question-role'>When you change your role, all your future requests will be deleted. Do you want to proceed, and change your role?</p>
                <div class='report-button-group'>
                    <button class='yes-button' @click='submitRole'>Yes</button>
                    <button class='cancel-button-2' @click='closePopup'>Cancel</button>
                </div>
            </div>
        </modal>
    </div>
    
</template>

<script>
	import axios from "axios";
	import { eventBus } from "../main";

	import firebase from 'firebase';

	export default {
        name: "account",
        data: function()
        {
            return {
                success: "",
                error: "",
                newRole: this.role,
            };
        },

		props: {
            role: String,
		},

		methods: {
            submitRole: function() {
                const params = {'newRole': this.newRole};

                axios.put('/users/role', params)
                .then((response) => {
                    this.success = "You have successfully changed your settings";
                    this.closePopup();
                    this.clearMessages();
                    eventBus.$emit('setRole', this.newRole);
                })
                .catch(err => {
                    alert(err.response.data.error);
                    this.errors.push(err.response.data.error)
                })
            },

            closePopup: function() {
                this.$modal.hide('Confirm');
            },

            submitSettings: function()
            {
                if (this.role === this.newRole) {
                    this.error = "Currently, you are already a " + this.role + ".";
                    this.clearMessages();
                } else {
                    this.$modal.show('Confirm');
                }
            },

            clearMessages: function() {
				setInterval(() => {
					this.success = "";
                    this.error = "";
				}, 5000);
			},
		},
	}

</script>
    
